import {promptBox} from 'nc-lightapp-front';
import {
    listCreate,
    listRefresh,
    listHeadDelete,
    listAttachment,
    listBillTrack,
    listCommit,
    listUnCommit,
    listLinkApprove,
    initDataBefore,
    onPrint,
    onOutput,
    listEdit
} from './listOperator';
import {LIST_BUTTON, STATUS, PRIMARY_KEY, LIST, LIST_DISABLED_BUTTON} from '../../constant';

//列表头部按钮操作
export function buttonClick(props, id, text, record, index) {
    switch (id) {
        //新增
        case LIST_BUTTON.create:
            listCreate(props);
            break;
        
        //修改
        case LIST_BUTTON.bodyUpdate:
            let selectDatas = props.table && props.table.getCheckedRows(LIST.table_id);
            let pk = selectDatas[0].data.values[PRIMARY_KEY.head_id] && selectDatas[0].data.values[PRIMARY_KEY.head_id].value;
            listEdit(props, pk)
            break;

        //删除
        case LIST_BUTTON.delete:
            promptBox({
                color: STATUS.warning,
                title: this.state.json['M0Z10301-000000'],/* 国际化处理： 删除*/
                content: this.state.json['M0Z10301-000001'],/* 国际化处理： 确定删除吗？*/
                beSureBtnClick: () => {
                    listHeadDelete({...props, json: this.state.json});
                    props.button.setButtonDisabled(LIST_DISABLED_BUTTON, true);
                }
            });
            
            break;

        //附件
        case LIST_BUTTON.attachment:
            listAttachment.call(this, {...props, json: this.state.json});
            break;

        //单据追溯
        case LIST_BUTTON.billTrack:
            listBillTrack.call(this, {...props, json: this.state.json});
            break;

        //审批详情
        case LIST_BUTTON.approvalLink:
            listLinkApprove.call(this, {...props, json: this.state.json});
            break;

        //提交
        case LIST_BUTTON.commit:
            listCommit.call(this, {...props, json: this.state.json});
            break;

        //收回
        case LIST_BUTTON.unCommit:
            listUnCommit({...props, json: this.state.json});
            break;

        //刷新
        case LIST_BUTTON.refresh:
            listRefresh({...props, json: this.state.json});
            break;

        //初始化数据
        case LIST_BUTTON.setInitData:
            initDataBefore({...props, json: this.state.json});
            break;
        //打印
        case LIST_BUTTON.print:
            onPrint({...props, json: this.state.json});
            break;
        //输出
        case LIST_BUTTON.output:
            onOutput.call(this,{ ...props, json: this.state.json });
            break;
    }
}